<template>
  <div style="padding:16px;overflow-y:auto;">

      <div v-if="news">
        <p style="font-size:18px;margin-bottom:16px;">{{news.tTitle}}</p>
        <p style="font-size:12px;margin-bottom:32px;color:#999;">
            <span>{{news.tAuthor}}</span>
            |
            <span>{{news.tDate}}</span>
        </p>
        <div v-html="news.tMsg"></div>
      </div>
      <p v-else style="font-size:12px;text-align:center;color:#999;">文章不存在</p>
      
  </div>
</template>

<script>
import { getArticle } from '@/api';
export default {
    data:()=>({
        news: null
    }),
    mounted(){
        this.getArticle();
    },
    methods: {
        getArticle(){
            let id = this.$route.params.id;
            getArticle({
                id
            }).then(res=>{
                if(res.code === 0){
                    this.news = res.data;
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        }
    }
}
</script>

